<template>
    <div>
        <div id="loginbox">
            <!-- <img style="opacity:0;width:100%" src="/resources/user/image/view_banner3.jpg" alt=""/> -->
            <div class="signin">
                <div class="signinlogo">
                    <img src="../../assets/navlogo.png" alt=""/>
                </div>
                <div class="signcont">
                    <p></p>
                    <div class="fillin">
                        <div class="creat">
                             <span> <router-link to="/login"></router-link></span>
                        </div>
                        <div class="fillinfo">
                            <p></p>
                            <input v-model="userMail" type="text" id="userMail" at="" class="required" placeholder="()"/>
                        </div>
                        <div class="fillinfo">
                            <p></p>
                            <input v-model="userName" type="text" id="userNm" at="" class="required" placeholder=""/>
                        </div>
                        <div class="fillinfo">
                            <p></p>
                            <input v-model="userPwd" type="password" id="pwd" at="" class="required password" placeholder=""/>
                        </div>
                        <!-- <div class="fillinfo codeinfo">
                            <div id="v_container"></div>
                            <input type="text" id="code_input" value="" placeholder=""/>
                        </div> -->
                        <button @click="registFun()" id="my_button"></button>
                    </div>
                    <i class="closebtn loginclo" @click="winClose()"></i>
                </div>
            </div>
        </div>
        <Footer></Footer>
    </div>
</template>

<script>
    import Footer from '@/components/footer'
    export default {
        name:"registUser",
        components:{
            Footer
        },
        data(){
            return{
                userMail:'',
                userName:'',
                userPwd:''
            }
        },
        methods:{
            registFun:function(){
                var _this = this
                if(_this.userMail==null||_this.userMail==''){
                     _this.$message({message: '',type: 'warning'})
                    return false;
                }
                if(_this.userName==null||_this.userName==''){
                     _this.$message({message: '',type: 'warning'})
                    return false;
                }
                if(_this.userPwd==null||_this.userPwd==''){
                     _this.$message({message: '',type: 'warning'})
                    return false;
                }
                _this.$ajax.post('/user/inter/regist.do',_this.$qs.stringify({userMail:_this.userMail,userNm:_this.userName,pwd:_this.userPwd,headImgId:1})).then(function(res){
                    if(res.data.flag){
                         _this.$message({message: ',(2)!',type: 'success'});
                    }
                    else{
                        _this.$message({message: res.data.msg,type: 'warning'})
                    }
                },function(){
                    _this.$message.error(',!');
                })
            },
            winClose:function(){
                window.close()
            }
        }
    }
</script>

<style lang="less" scoped>
    @import '../../styles/user.css';
</style>
<style lang="less">
    .el-message{
        top:40%;
    }
</style>


