<template>
    <div>
        <Header foo="foo"></Header>
        <div class="about-jkv-detail">
            <div class="ourbanner">
                <img src="../assets/about/ourbanner.jpg" alt=""/>
                <h2 class="ourtit"></h2>
            </div>
            <div class="comwrap">
                <div class="ourtext">
                    <p class="text-title">
                         <i class="circle_i"></i>
                    </p>
                    <div class="ourintro">
                        2003
                    </div>
                    <div class="ourintro">
                        2016
                    </div>
                    <div class="ourintro">
                        
                    </div>
                    <div class="ourintro">
                        
                    </div>
                    <div class="ourintro ourintrolast">
                        
                    </div>
                    <div class="ourintroimg">
                        <img src="../assets/about/ourintro.png" alt=""/>
                    </div>
                    <p>
                        <i class="circle_i"></i><img src="../assets/about/ourworld.png" alt=""/>
                    </p>
                    <div class="ourintro">
                         <br/>
                        To the worldyou may be one personbut to one personyou may be  the world
                    </div>
                </div>
            </div>
        </div>
        <Footer link="home"></Footer>
    </div>
</template>
<script>
import Header from '@/components/header';
import Footer from '@/components/footer';
export default {
    name:"aboutJkvView",
    components:{
        Header,Footer
    }
}
</script>
<style lang="less" scoped>
    .about-jkv-detail{
        width: 100%;
        background: #fff;
        .ourbanner{
            width:100%;
            height:400px;
            position:relative;
            overflow:hidden;
            img{
                width:100%;
                min-width:1100px;
                position:absolute;
                top:50%;
                left:50%;
                transform:translate(-50%,-50%);
                -ms-transform:translate(-50%,-50%);
                -moz-transform:translate(-50%,-50%);
                -webkit-transform:translate(-50%,-50%);
                -o-transform:translate(-50%,-50%);
            }
            .ourtit{
                font-size:34px;
                position:absolute;
                top:50%;
                left:50%;
                transform:translate(-50%,0);
                 -ms-transform:translate(-50%,0);
                -moz-transform:translate(-50%,0);
                -webkit-transform:translate(-50%,0);
                -o-transform:translate(-50%,0);
                color:#fff;
                font-weight:400;
            }
        }
        .comwrap{
            .ourtext{
                width:800px;
                margin:0 auto;
                padding:30px 0 0;
                .circle_i{
                    position:absolute;
                    top:50%;
                    left:-20px;
                    width:8px;
                    height:8px;
                    margin-top:-4px;
                    border-radius:10px;
                    background:#333;
                }
                p{
                    position:relative;
                }
                .ourintro{
                    line-height:24px;
                    padding:20px 0;
                    color: #333;
                    font-size: 14px;
                    border-bottom:1px dotted #666;
                }
                .ourintrolast{
                    border:none;
                }
                .ourintroimg {
                    text-align:center;
                    margin:20px 0 30px;
                }
                .text-title{
                    font-size:24px;
                }
            }
        }
    }

</style>


