<template>
  <div>
    <Header link="news"></Header>
    <div id="topNav"></div>
    <!--<div class="n-bread-crumb">
      <div class="content">
        <span></span>
        <router-link target="_blank" to="/news/news"></router-link>
        
        <span></span>
      </div>
    </div>-->
    <div class="bread-crumb-container">
      <div class="bread-crumb">
        <span></span>
        <router-link target="_blank" to="/news"></router-link>
        
        <span></span>
      </div>
    </div>
    <div class="n-search-container">
      <div class="content">
        <div class="search-top">
          <div class="left"></div>
          <div class="right">
            <div class="no-limit" :class="typeNo?'th-cli':''" @click="typeNoFun()"></div>
            <ul class="type-list">
              <li :class="typeCheckedId == item.cateId?'th-cli':''" v-for="(item,index) in typeList" :key="index"
                  @click="typeFun(item.cateId)">
                {{item.cateNm}}
              </li>
            </ul>
          </div>
        </div>

        <div class="search-panel">
          <input id="chooseLabel" type="text" placeholder="" v-model="keyWord"/>
          <button @click="getPage()"></button>
          <ul class="lab-list"></ul>
        </div>
      </div>
    </div>
    <div class="n-list">
      <ul>
        <li class="list-2" v-for="item in pageList">
          <div>
            <span class="1-1"></span>
            <span class="1-2"></span>
          </div>
          <router-link target="_blank" :to='{path:"/policyDetail/"+item.poId}'>{{item.poTitle}}</router-link>
        </li>
      </ul>
    </div>
    <!--<div class="page" id="page"></div>-->
    <div class="page-container">
      <div class="content">
        <el-pagination
          :total="totalNum"
          :page-size="pageData.rows"
          @current-change="pageFun"
          background
          layout="prev, pager, next">
        </el-pagination>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
  import Header from '@/components/header'
  import Footer from '@/components/footer'

  export default {
    data() {
      return {
        continentList: [],
        typeList: [],
        areaNo: true,
        areaContinent: null,
        areaCountry: null,
        countryList: [],
        typeNo: true,
        typeCheckedId: null,
        pageData: {
          page: 1,
          rows: 2,
          poType: "",
          labNm: "",

        },
        totalNum:0,
        pageList: [],
        actNum: 0,
        keyWord: "",//
        labArr: []//
      };
    },
    components: {
      Header, Footer
    },
    created() {
      //this.getRegionList();
      this.getTypeList();
      this.getPage();
      this.getKeyWordList();
    },
    methods: {
    /*  //()
      getRegionList: function () {
        let _this = this;
        _this.$ajax.post('/cnews/tips/inter/getRegionList.do', _this.$qs.stringify({regLevel: 1})).then(function (res) {
          if (res.data.flag) {
            _this.continentList = res.data.list;
            console.log(_this.continentList)
          }
        })
      },*/
      //
      getTypeList: function () {
        let _this = this;
        _this.$ajax.post('/cnews/policy/inter/getPolicyTypeList.do').then(function (res) {
          if (res.data.flag) {
            _this.typeList = res.data.list;
            console.log(_this.typeList)
          }
        })
      },
      /*//
      getCountry: function (regId) {
        var _this = this;
        _this.$ajax.post('/cnews/tips/inter/getRegionList.do', _this.$qs.stringify({
          regLevel: 2,
          pId: regId
        })).then(function (res) {
          if (res.data.flag) {
            _this.countryList = res.data.list;
            console.log(_this.countryList)
          }
        }, function () {

        });
        _this.areaContinent = regId;
        // _this.areaNo = false
        _this.areaCountry = null

      },
      //
      areaNoFun: function () {
        var _this = this;
        _this.areaNo = true;
        _this.areaContinent = null;
        _this.countryList = [];
        _this.areaCountry = null;
        _this.pageData.rSec = "";
        //
        this.getPage();
      },
      //
      countryFun: function (countryId) {
        this
        this.areaNo = false;
        // _this.areaCou = null
        this.areaCountry = countryId;
        this.pageData.rSec = countryId;
        //
        this.getPage();
      },*/
      //
      typeNoFun: function () {
        var _this = this;
        _this.typeNo = true;
        _this.typeCheckedId = null;
        _this.pageData.poType = "";
        //
        this.getPage();
      },
      typeFun: function (cateId) {
        this.typeNo = false;
        this.typeCheckedId = cateId;
        this.pageData.poType = cateId;
        //
        this.getPage();
      },
      //
      getPage: function () {
        var _this = this;
        _this.pageData.labNm=_this.keyWord;// 
        _this.$ajax.post('/cnews/policy/inter/getPolicyPageList.do', _this.$qs.stringify(_this.pageData)).then(function (res) {
          _this.pageList = res.data.list;
          _this.totalNum=res.data.pageTotal;
          console.log("pageList: "+_this.pageList)

        }, function () {

        });
        // _this.areaContinent = regId
        // _this.areaNo = false
        // _this.areaCountry = null

      },
      getKeyWordList: function () {
        var _this = this;
        _this.$ajax.get('/cnews/basedate/inter/getKeyList.do').then(function (res) {

          if (res.data.flag) {
            if (res.data.keyList && res.data.keyList.length && res.data.keyList.length > 0) {
              $.each(res.data.keyList, function (i, v) {
                _this.labArr.push(v.cateNm);
              });
              _this.bindLabSearch();
            }
          }
          console.log("" + res.data.keyList)

        }, function () {

        });
      },
      bindLabSearch: function () {
        let _this = this;
        $("#chooseLabel").bind('input propertychange', function () {
          if ($('#chooseLabel').val() != '') {
            $(".search-panel button").css('background', 'url(../../assets/news/new/ss_b.png) no-repeat');
          } else {
            $(".search-panel button").css('background', 'url(../../assets/news/new/ss_a.png) no-repeat');
          }
          /**/
          /**/
          var arrNew = [];
          var i;
          for (i = 0; i < _this.labArr.length; i++) {
            var arrItems = _this.labArr[i];
            if ($.inArray(arrItems, arrNew) == -1) {
              arrNew.push(arrItems);
            }
          }
          /*inputdiv*/
          for (i = 0; i < arrNew.length; i++) {
            var arrWord = arrNew[i].toString();
            /*console.log(arrWord);*/
            if (((arrWord.indexOf($('#chooseLabel').val())) > -1) && ($('#chooseLabel').val().length > 0)) {
              var addArrWord = "<li>" + arrWord + "</li>";
              $(".search-panel ul").append(addArrWord);
//						$(".search-panel ul").removeClass("auto-hidden");
            }
          }
          /*divinput*/
          $(".search-panel ul li").each(function () {
            if ($(this).text().indexOf($('#chooseLabel').val()) < 0) {
              $(this).remove();
            }
            else if ($('#chooseLabel').val().length == 0) {
//						$(".search-panel ul").addClass("auto-hidden");
              $(".search-panel ul li").remove();
            }
          });
          /*div*/
          $(".search-panel ul li").each(function (i, iText) {
            var iTextHtml = iText.innerHTML;
            $(".search-panel ul li").each(function (j, jText) {
              var jTextHtml = jText.innerHTML;
              if (i < j && iTextHtml == jTextHtml) {
                $(this).remove();
              }
            });
          });
          /*div*/
          $(".search-panel ul li").on("click", function () {
            //$("#chooseLabel").val($(this).text());
            _this.keyWord=$(this).text();
            _this.pageData.labNm =_this.keyWord;
//					$(".search-panel ul").addClass("auto-hidden");
            $(".search-panel ul li").remove();

            //searchType();
          });

        });
      },

      pageFun: function (res) {
        console.log(res)
        this.pageData.page = res
        this.getPage()
      }

    }
  };
</script>

<style lang="less">
  @import '../../styles/newsList.css';

  #topNav {
    padding-top: 110px;
  }

  .n-search-container {
    margin-top: 20px;
  }
</style>



